@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Helvetica:wght@400;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-helvetica: Helvetica;
  --font-poppins: Poppins;

  /* font sizes */
  --font-size-base: 16px;
  --font-size-sm: 14px;
  --font-size-lg: 18px;
  --font-size-xs: 12px;
  --font-size-xl: 20px;
  --font-size-13xl: 32px;
  --font-size-3xs: 10px;

  /* Colors */
  --color-white: #fff;
  --color-gainsboro: #e2e2e2;
  --color-black: #000;
  --color-crimson: #c41239;
  --color-gray: #797979;
  --color-olivedrab-100: #5c700e;
  --color-olivedrab-200: rgba(92, 112, 14, 0.1);
  --color-whitesmoke: #f7f7f7;

  /* Gaps */
  --gap-6xl: 25px;
  --gap-base: 16px;
  --gap-2xs: 11px;
  --gap-8xs-9: 4.9px;
  --gap-7xs: 6px;
  --gap-12xs-6: 0.6px;

  /* Paddings */
  --padding-83xl-4: 102.4px;
  --padding-xs: 12px;
  --padding-11xs: 2px;
  --padding-base: 16px;
  --padding-lg: 18px;
  --padding-23xl: 42px;
  --padding-22xl-9: 41.9px;
  --padding-9xs: 4px;
  --padding-3xs-3: 9.3px;
  --padding-7xs: 6px;
  --padding-3xs: 10px;

  /* border radiuses */
  --br-xl: 20px;
  --br-9xs: 4px;
  --br-13xl: 32px;
  --br-5xs: 8px;
  --br-9xl: 28px;
}
